<template>
    <div>
        <input v-model="sousou" placeholder="输入歌曲名字/歌手/歌词" @change="slgedan"  style="width: 60%;margin-top: 20px;margin-bottom: 10px;height: 20px">
        <ul class="gedan">
            <li v-for="song in ret" :key="song.id" class="gelan">
                <div class="mvimg">
                    <img :src="song.cover" alt="" @click="bofang(song)">
                </div>
                <div class="mvjj">
                    <h3  class="mname">{{song.name}}</h3>
                    <p><span style="font-weight: bold;padding-right: 10px;">播放量:</span>{{song.playCount}}</p>
                </div>

            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "mv",
        data(){
            return{
                sousou:"",
                ret:[]
            }
        },
        methods: {
            slgedan() {
                this.$axios.get("https://api.imjad.cn/cloudmusic/", {
                    params: {
                        type: "search",
                        s: this.sousou,
                        search_type: 1004
                    }
                }).then(res => {
                    // console.log(res)
                    if (!res.data.result) {
                        alert("搜索结果为空！")
                    } else {
                        this.ret = res.data.result.mvs
                        // console.log(this.ret)
                    }
                })
            },
            bofang(song){
                this.$axios.get("https://api.imjad.cn/cloudmusic/",{
                    params:{
                        type: "mv",
                        id: song.id,
                        search_type: 1004
                    }
                }).then(res=>{
                    // console.log(res.data.data)
                    this.$router.push({
                        name:"mvbofang",
                        params:{
                            mvlist: res.data.data
                        }
                    })
                })
            }
        }
    }
</script>

<style scoped>
    .gedan{
        width: 100%;
        margin: 10px auto;
        padding: 0;
        list-style: none;
        background: white;
    }
    .gelan{
        width: 95%;
        height: 110px;
        overflow: hidden;
        line-height: 50px;
        text-align: left;
        padding: 10px;
        padding-bottom: 0px;
        /*display: flex;*/
        /*justify-content: space-between;*/
    }
    .gelan>div{
        float: left;
    }
    .mvimg>img{
        width: 160px;
        cursor: pointer;
    }
    .mvimg>img:hover{
        opacity: 0.7;
    }
    .mvjj{
        height: 90px;
        line-height: 90px;
        width: calc(96% - 160px);
        padding-left: 20px;
        border-top: 1px #eee solid;
        border-bottom: 1px #eee solid;
        box-sizing: border-box;
    }
    .mvjj>h3{
        font-size: 16px;
        line-height: 30px;
        margin: 0;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
    }
    .mvjj>p{
        margin: 0;
        line-height: 30px;
    }

</style>

